.aside {
  &__container {
    @include media(notebook) {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      column-gap: 20px;
    }
  }

  &__content {
    @include media(notebook) {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      column-gap: 20px;
      grid-column: 1 / -1;
      justify-content: start;
    }
  }

  &__title {
    color: $c-green-1;
    text-transform: uppercase;
    @extend %h3;

    @include media(tablet) {
      margin: 0 auto;
      text-align: center;
    }

    @include media(notebook) {
      grid-column: 7 / 11;

      width: 100%;

      text-align: left;
      font-size: 24px;

      &::after {
        content: "";
        position: relative;
        bottom: -100px;
        right: 0;

        display: block;
        height: 1px;
        margin-right: -2000px;

        grid-column: 7 / -1;
        grid-row: 5;

        background-color: rgba(52, 79, 16, 0.5);
      }
    }
  }

  &__about {
    position: relative;
    display: none;
    color: $c-dark-main;
    @extend %text-reg-16;

    @include media(notebook) {
      display: block;
      grid-column: 2 / 6;
      grid-row: 1;

      &::before {
        content: "";
        position: relative;
        top: -100px;

        display: block;
        height: 1px;
        margin-left: -2000px;

        grid-column: 1 / 6;
        grid-row: 1;

        background-color: rgba(52, 79, 16, 0.5);
      }
    }
  }
}
